@import "fonts.scss";
@mixin font ($font-family, $color, $font-size, $line-height){
	font-family: $font-family, cursive;
	color: $color;
	font-size: $font-size;
	line-height: $line-height;
}
@mixin position ($flex-direction, $justify-content) {
	display: flex;
	flex-direction: $flex-direction;
	justify-content: $justify-content;
	align-items: center;
}
@mixin btn-active ($bg-color, $height, $width) {
	box-shadow: 0 2px 3px rgba(223, 81, 40, 0.48), inset 0 1px 0 rgba(251, 96, 51, 0.53);
	border: 1px solid rgba(234, 71, 34, 0.83);
	border-radius: 3px;
	background-color: $bg-color;
	height: $height;
	width: $width;
}
@mixin btn-grey ($bg-color, $height, $width) {
	border: 1px solid #bcbcbc;
	border-radius: 3px;
	background-color: $bg-color;
	background-image: linear-gradient(to top, #cecece 0%, #d9d9d9 87%, #d9d9d9 100%);
	height: $height;
	width: $width;
}

@mixin icon ($height, $width) {
	height: $height;
	width: $width;
	display: inline-block;
	vertical-align: middle;
}
@keyframes appearance {
	0% {opacity: 0;}
	100% {opacity: 1;}
}
