@import "fonts.scss";
@import "variables.scss";
@import "mixins.scss";



body {
    font-size: 14px;
    line-height: normal;
    text-align: center;
    vertical-align: middle;
    font-family: "AvenirNextCyr", Arial, Helvetica, sans-serif;
}
.menu {
  background-color: #555656;
  padding-top: 20px;
     &__text {
         font-size: 1.5rem;
         color: #fff;
         text-transform: uppercase;
     }
     &__brand img {
         width: 50%;
     }
     &__text i {
         color: #f3e41e;
     }
     &__prompt {
          color: #fff;
          text-align: center;
     }
     &__prompt-dotted {
         width: auto;
         padding: 5px 0;
         margin: 0 auto;
         border-bottom: 1px dashed #fff;
         color: #fff!important;
     }
     &__search {
         display: block;
         background: #e0e0df url('../img/icons/search.png') center right no-repeat;
         border: none;
         border-radius: 5px;
         width: 100%;
         height: 55px;
         padding-left: 20px;
         color: #5d5c59;
         font-size: 1.5rem;
         text-transform: uppercase;
         margin: 2vh 0;
     }
     &__button {
         display: block;
         background-color: #e92727;
         border: none;
         border-radius: 5px;
         cursor: pointer;
         width: 100%;
         height: 55px;
         margin-left: 5px;
     }
     &__list {
         margin: 0 auto;
         width: 100%!important;
     }
     &__link {
         font-size: 1.2rem;
         color: #fff!important;
         text-transform: uppercase;
         white-space: nowrap;
         border-bottom: 2px solid #555656;
     }
     &__link:hover, &__link:focus, {
         border-bottom-color: #fff;
     }
     &__btn-hidden {
         display: none;
     }

}
.menu2 {
    background: url('../img/menu2-bg.png') top left repeat-x;
    &__wrapp {
        background-color: #e92727;
        height: 70px;
    }
    &__list {
        margin: 0;
    }
    &__item {
        padding-top: 40px;
    }
    &__item-msg {
        background: url('../img/icons/msg.png') top center no-repeat;
    }
    &__item-on-call {
        background: url('../img/icons/on-call.png') top center no-repeat;
    }
    &__item-callback {
        background: url('../img/icons/callback.png') top center no-repeat;
    }
    &__link {
        font-size: 0.8rem;
        color: #fff;
        text-transform: uppercase;
        text-decoration: none;
        white-space: nowrap;
        padding: 5px;
    }

    &__link:hover, &__link:focus {
        color: #f3e41e;
        text-decoration: none;
    }
    &__button, &-yellow {
        display: block;
        width: 30%;
        background-color: #f3e41e;
        border-radius: 5px;
        border: none;
        margin: 3px 0;
        color: #000;
        text-transform: uppercase;
        text-align: center;
        vertical-align: middle;
        font-size: 1.5rem;
        height: 64px;
    }
    &-yellow a {
        color: #000;

    }
    &__button {
        background: #f3e41e url('../img/icons/menu2-btn.png') 5% 50% no-repeat;
    }
    &-yellow {
        background: #f3e41e url('../img/icons/menu2-btn2.png') 3% 50% no-repeat;
        padding-left: 30px;
    }
    &__text {
        font-size: 1rem;
        text-transform: none;
    }
}
.slider {
    width: 100%;
    margin: 20px 0;
    &__wrapp {
        width: 100%;
        background-color: #fbfbfb;
    }
    &__title {
        color: #e92727;
        margin: 20px 7vw;
        font-size: 1.3rem;
        font-weight: bolder;
    }
    &__text {
        font-size: 1.1rem;
        margin: 0 7vw;
        font-weight: normal;
    }
    .carousel-indicators li {
        width: 10px;
        height: 10px;
        border-radius: 50%;
        opacity: 1;
    }
    .carousel-indicators .active {
        background-color: red;
    }

}
.product {
    background-color: #fff;
    text-align: center;
    padding: 10px 5px;
    &__name {
        font-size: 1.1rem;
        text-transform: uppercase;
        text-align: center;
    }
    &__img {
        border: none;
    }
    &__price {
        text-align: center;
        span {
            color: #e92727;
            font-size: 1.4rem;
        }
    }
    &__btn {
        display: block;
        width: 80%;
        background-color: #f3e41e;
        border-radius: 5px 5px 10px 10px;
        border: none;
        height: 30px;
        margin: 5px auto;
        text-transform: uppercase;
    }
    &__btn-red {
        background-color: #e92727;
        color: #fff;
    }
}
.section__title {
    font-size: 1.5rem;
    text-transform: uppercase;
    text-align: center;
    margin: 40px 0 15px;
}
.title-btn {
    display: block;
    width: 30%;
    background-color: #f3e41e;
    border-radius: 5px 5px 10px 10px;
    border: none;
    height: 64px;
    padding: 20px 0;
}
.title-btn-red {
    color: #fff;
    background-color: #e92727;
}
.title-black {
    font-size: 1.3rem;
    text-transform: uppercase;
    margin: 10px 0 15px;
}
.all-catalog {
    width: auto;
    padding: 10px 10px;
    margin: 40px 0 15px 5vw;
    font-size: 1rem;
}
.seo-text {
    font-size: 1.1rem;
    line-height: 1.5rem;
    text-align: left;
}
.offer {
    &__block {
        color: #000;
        padding-left: 50px;
        text-align: left;
    }
    &__block a {
        color: #000;
    }
    &__title {
        font-size: 1.2rem;
        margin: 15px 0 20px;
    }
    &-tailor {
        background: url('../img/icons/tailor.png') left 15px no-repeat;
        background-size: 15%;
    }
    &-stitch {
        background: url('../img/icons/stitch.png') left 15px no-repeat;
        background-size: 15%;
    }
    &-knit {
        background: url('../img/icons/knit.png') left 15px no-repeat;
        background-size: 15%;
    }
    &-iron {
        background: url('../img/icons/iron.png') left 15px no-repeat;
        background-size: 15%;
    }
    &-clean {
        background: url('../img/icons/clean.png') left 15px no-repeat;
        background-size: 15%;
    }
    &-accessories {
        background: url('../img/icons/accessories.png') left 15px no-repeat;
        background-size: 15%;
    }

    &-1 {
        background: url('../img/icons/offer1.png') left 15px no-repeat;
    }
    &-2 {
        background: url('../img/icons/offer2.png') left 15px no-repeat;
    }
    &-3 {
        background: url('../img/icons/offer3.png') left 15px no-repeat;
    }
    &-4 {
        background: url('../img/icons/offer4.png') left 15px no-repeat;
    }
    &-5 {
        background: url('../img/icons/offer5.png') left 15px no-repeat;
    }
    &-6 {
        background: url('../img/icons/offer6.png') left 15px no-repeat;
    }
    &__block a:hover, &__block a:focus {
        color: #e92727;
        text-decoration: none;
    }
}

.question {
    border: none;
    border-radius: 5px;
    background-color: #f3e41e;
    padding: 10px 20px;
    text-align: left;
}
.answer {
    margin: 15px 20;
}
.footer {
    background-color: #f7f6f1;
    &__menu {
        background-color: #555656;
        a:hover, a:focus {
            color: #f3e41e!important;
            border-bottom-color: #f3e41e;
        }
    }
    &__link {
        font-size: 1.1rem;
        color: #000;
        text-decoration: none;
    }
    &__text-small {
        font-size: 0.7rem;
        margin-top: 15px;
    }
    &-bg {
        background: url('../img/footer-bg.png') top left no-repeat;
        background-size: cover;
        padding: 20px 0 30px;
    }
}

.contacts-fixed {
    position: fixed;
    top: 280px;
    right: 10px;
}
.contact__link {
    display: block;
    color: white;
    background-color: grey;
    height:25px;
    width:25px;
    border-radius: 3px;
    margin: 3px 3px;
    padding-top: 5px;
    text-align: center;
    vertical-align: middle;
    &:hover, &:focus {
        color: white;
        background-color: green;
    }
    &-yellow {
        color: #f3e41e;
        background-color: #333232;
    }
    &-yellow:hover, &-yellow:focus {
        color: white;
        background-color: #333232;
    }
}
.footer-gift {
    border-top: 1px solid #b2b2b2;
    &__text {
        font-size: 1.2rem;
        text-transform: uppercase;
        padding: 10px 0;
        span {
            color: #e92727;
        }
    }
}
.text-left {
    text-align: left;
}
.margin-vertical {
    margin: 7vh auto;
}
.no-padding {
    padding-left: 0;
    padding-right: 0;
    @media screen and (max-width: 576px) {
        padding-left: 10px;
        padding-right: 10px;
    }
}
.carousel-control-prev-icon {
    background-image: url('../img/icons/arrow-prev.png');
    position:absolute;
    top: center;
    left: 10px;
    background-size: contain;
}
.carousel-control-next-icon {
    background-image: url('../img/icons/arrow-next.png');
    position:absolute;
    top: center;
    right: 10px;
    background-size: contain;
}

