.section {
    margin-top: 43px;
    &__head {
        width: 100%;
        background-image: linear-gradient(#eee, #eee), linear-gradient(#eee, #eee), linear-gradient(#eee, #eee), linear-gradient(#eee, #eee);
        background-size: 100% 1px, 100% 1px, 100% 1px, 100% 1px;
        background-position: left top 9px, left top 12px, left top 15px, left top 18px;
        background-repeat: no-repeat;
    }
    &__name {
        @include font ($menu-font, #747272, $size18, 24px);
        background-color: #fff;
        margin-bottom: 0;
        padding-right: 5px;
    }
    &__btn {
        @include btn-active (#fa5b33, 25px, 25px);
        position: relative;
        span {
          display: block;
          position: absolute;
          height: 3px;
          width: 15px;
          background-color: $white;
          opacity: 1;
          z-index: 2;
          left: 5px;
        }
        span:nth-child(1) {
          top: 11px;
        }
        span:nth-child(2) {
          transform: rotate(90deg);
          top: 11px;
        }
    }
    &__btn.state_open span:nth-child(2) {
      transform: rotate(0deg);
      top: 11px;
    }

    &__body {
        margin: 34px auto;
        @include position (row, space-between);
    }


    .brend {
        padding: 20px 0;
        transition: 1.5s;
    }
    .brend:hover {
        cursor: pointer;
        transform: scale(1.2);
    }

    .swiper-button-next, .swiper-container-rtl .swiper-button-prev {
        background-image: url('../img/icons/arrow-next.png');
        background-size: 35px 37px;
        width: 35px;
        height: 37px;
        right: 0px;
        left: auto;
    }
    .swiper-button-prev, .swiper-container-rtl .swiper-button-next {
        background-image: url('../img/icons/arrow-prev.png');
        background-size: 35px 37px;
        width: 35px;
        height: 37px;
        left: 0px;
        right: auto;
    }
}
.product {
    border: 1px solid #bcbcbc;
    display: flex;
    justify-content: center;
    background-color: #d9d9d9;
    @media screen and (max-width: 768px) {
         margin: 5px auto;
    }
    &:first-child {
        margin-left: 0;
    }
    &:last-child {
        margin-right: 0;
    }
    &__img {
        margin: 5px;
        z-index: 2;
        border: 1px solid #bcbcbc;
        box-shadow: inset 1px 2px 1px rgba(255, 255, 255, 0.58);
        position:relative;
        img {
            z-index: 2;
            width: 100%;
            height: auto;
        }
        .btn-hover {
            display: none;
            position: absolute;
            top: 20%;
            left: 17%;
            z-index: 4;
            @media screen and (max-width: 992px) {
                left: 10%;
                top: 10%;
            }
            @media screen and (max-width: 768px) {
                left: 17%;
                top: 20%;
            }
            @media screen and (max-width: 490px) {
                left: 10%;
                top: 10%;
            }
            button {
                @include btn-grey (#d9d9d9, 32px, 135px);
                margin: 10px auto;
                @include font ($Myriad-font, #494949, $size14, 28px);
                @media screen and (max-width: 490px) {
                    width: 80%;
                    line-height: 15px;
                    margin: 5px auto;
                }
                @media screen and (max-width: 768px) {
                    width: 135px;
                    line-height: 28px;
                    margin: 10px auto;
                }
                @media screen and (max-width: 490px) {
                    width: 80%;
                    line-height: 15px;
                    margin: 5px auto;
                }
            }
            button:hover, button:focus  {
                box-shadow: 0 2px 3px rgba(255, 255, 255, 0.58), inset 0 1px 0 rgba(251, 251, 251, 0.53);
                border: 1px solid #bcbcbc;
                background-color: #fd5f37;
                background-image: linear-gradient(to top, #a9a9a9 0%, #a9a9a9 13%, #878787 100%);
                color: $white;
            }
        }
        &:hover, &:focus {
            img {
                opacity: 0.54;
            }
            .btn-hover {display: block};
        }
    }
    &__title {
        @include font ($navigation-font, #494949, $size16, 28px);
        margin-left: 5px;
        @media screen and (max-width: 490px) {
            font-size: 13px;
        }
    }

    &__btn {
        @include btn-active (#fa5b33, 28px, 109px);
        @include font ($navigation-font, $white, $size14, 14px);
        text-transform: uppercase;
        margin-left: 5px;
        @media screen and (max-width: 992px) {
             font-size: 10px;
        }
        @media screen and (max-width: 768px) {
             font-size: 12px;
        }
        @media screen and (max-width: 410px) {
             font-size: 8px;
             white-space: nowrap;
             margin-left: 2px;
        }
        &:hover, &:focus {
            box-shadow: 0 2px 3px rgba(255, 255, 255, 0.58), inset 0 1px 0 rgba(251, 251, 251, 0.53);
            border: 1px solid #bcbcbc;
            background-color: #fd5f37;
            background-image: linear-gradient(to top, #a9a9a9 0%, #a9a9a9 13%, #878787 100%);
            color: $white;
        }
    }

    &__price {
        margin-right: 5px;
        @include font ($price-font, $white, $size18, 28px);
        @media screen and (max-width: 410px) {
             margin-right: 2px;
        }
        .price-dollar {
            padding: 2px 4px;
            border: 1px solid rgba(91, 91, 91, 0.79);
            box-shadow: 0 1px 0 rgba(255, 255, 255, 0.48);
            background-color: #646464;
            background-image: linear-gradient(to top, #444444 0%, #646464 99%, #646464 100%);
            @media screen and (max-width: 410px) {
                 padding: 2px 2px;
            }
        }
        .price-number {
            color: #636363;
            padding: 2px 4px;
            border: 1px solid rgba(91, 91, 91, 0.79);
            box-shadow: 0 1px 0 rgba(255, 255, 255, 0.48);
            background-color: #646464;
            background-image: linear-gradient(to top, #cecece 0%, #cecece 1%, #eaeaea 100%);
            @media screen and (max-width: 410px) {
                 padding: 2px 2px;
            }
            &:last-child {
                border-left: none;
            }
        }
    }
}
.product-padding {
    @media screen and (max-width: 410px) {
          padding: 0 10px;
    }
}
.prompts__section {
    background-color: #f4ebcd;
    background: repeating-linear-gradient(90deg, #faf1d2, #faf1d2 1px, #efe6c9 1px, #efe6c9 2px);
    margin-top: 43px;
    box-shadow: 0 5px 0 rgba(222, 213, 183, 0.92);

    .prompt {
        margin-top: 20px;
        .prompt1 {
            background: url('../img/icons/light.png') center left no-repeat;
        }
        .prompt2 {
            background: url('../img/icons/lightning.png') center left no-repeat;
        }
        .prompt3 {
            background: url('../img/icons/power.png') center left no-repeat;
        }

        &__title {
            @include font ($menu-font, #747272, $size18, 24px);
            padding-left: 30px;
            padding-bottom: 8px;
            margin-bottom: 0;
        }
        &__text {
            @include font ($navigation-font, #7d7253, $size14, 24px);
            text-align: justify;
            padding-left: 30px;
        }
    }
}

.information {
    background-color: #636363;
    padding-top: 20px;
    padding-bottom: 40px;
    &__title {
        @include font ($menu-font, $white, $size18, 24px);
    }
    &__text, &__text a {
        @include font ($navigation-font, #c9c9c9, $size14, 24px);
    }
}

.copyright {
    background-color: #4f4f4f;
    background-image: linear-gradient(to top, #4f4f4f 0%, #494949 100%);
    border-top: 5px solid #5c5c5c;
    &__text {
        padding: 27px 0 31px;
        @include font ($menu-font, #848484, $size14, 24px);
        p { margin: 0;}
        span {
            @include font ($Myriad-font, #848484, $size12, 24px);
        }
    }
}


.animated-modal .product {
    button {
        display: none;
    }
    border: none;
}

.slideshow-container {
    position: relative;
    width: 100%;
    .product__slides {
        display: none;
    }
    .prev, .next {
    cursor: pointer;
    display: block;
